import React from "react";

import "./Work.scss";

const Job = ({
	image,
	companyName,
	jobPost,
	jD,
	loc,
	startDate,
	endDate,
	techStack,
}: {
	image: JSX.Element;
	companyName: string;
	jobPost: string;
	jD: JSX.Element;
	loc: string;
	startDate: string;
	endDate: string;
	techStack?: Array<any>;
}): JSX.Element => (
	// <div className="column is-half-tablet is-full-mobile is-two-fifths-desktop">
	<div className="column">
		<div className="card">
			<div className="card-content">
				<article className="media">
					<div className="media-left">
						<figure className="image is-64x64">
							{image}
						</figure>
					</div>
					<div className="media-content no-overflow">
						<p className="title is-4">{companyName}</p>
						<p className="subtitle is-6">{jobPost}</p>
					</div>
				</article>

				<div className="content">
					<nav className="level is-mobile">
						<span className="level-left">
							<span className="level-item">
								{loc ? (
									<span>
										<span className="icon">
											<i className="fas fa-map-marker-alt"></i>
										</span>
										{loc}
									</span>
								) : (
									<></>
								)}
							</span>
						</span>
						<span className="level-right">
							<span className="level-item">
								{startDate && endDate ? (
									<span>
										<span className="icon">
											<i className="fas fa-calendar-alt"></i>
										</span>
										{startDate}-{endDate}
									</span>
								) : (
									<></>
								)}
							</span>
						</span>
					</nav>

					{jD}
				</div>

				{techStack && techStack.length > 0 ? (
					<footer className="tags has-text-centered is-medium">
						{techStack.map((tech) => {
							return (
								<span className="tag" key={tech.name}>
									<span>
										<img src={tech.logo} alt={`${tech.name} Logo`} />
									</span>
									{tech.name}
								</span>
							);
						})}
					</footer>
				) : (
					<></>
				)}
			</div>
		</div>
	</div>
);

export default Job;
