import React, { useState } from "react";

function ModalAndTriggerFolder({
	skills,
	shortHeading,
	longHeading,
}: {
	skills: any;
	shortHeading: string;
	longHeading: string;
}) {
	const [disp, setDisp] = useState(false);

	return (
		<>
			<div className={`modal ${disp ? "is-active" : ""}`}>
				<div className="modal-background"></div>
				<div className="modal-card">
					<header className="modal-card-head">
						<p className="modal-card-title">{longHeading}</p>
						<button
							className="delete"
							aria-label="close"
							onClick={() => setDisp(false)}
						></button>
					</header>
					<div className="modal-card-body">
						<div>
							{Object.keys(skills).map((skillHeader) => (
								<div className="content">
									<h4 className="is-capitalized">{skillHeader}</h4>
									<div className="columns is-variable is-multiline is-mobile">
										{skills[skillHeader].map((skill: any) => (
											<div
												className="column is-2-desktop is-2-tablet is-4-mobile has-text-centered"
												key={skill.name}
											>
												<div>
													<img src={skill.logo} alt={`${skill.name} Logo`} />
													<p>
														<a
															href={skill.link}
															target="_blank"
															rel="noopener noreferrer"
														>
															{skill.name}
														</a>
													</p>
												</div>
											</div>
										))}
									</div>
									<hr />
								</div>
							))}
						</div>
					</div>
				</div>
			</div>
			<div className="has-text-centered">
				<div className="folder is-6-mobile" onClick={() => setDisp(true)}>
					{Object.values<any>(skills)
						.reduce((prev, curr) => [...prev, ...curr])
						.map((skill: any) => (
							<img
								src={skill.logo}
								alt={`${skill.name} logo`}
								key={skill.name}
							/>
						))}
				</div>
				<p>{shortHeading}</p>
			</div>
		</>
	);
}

export default ModalAndTriggerFolder;